@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
#landing-container {
    background-color: var(--color-primary);
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

#landing-panel {
    width: 80%;
    max-width: 20rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#landing-panel,
#landing-panel a {
    color: var(--color-title-in-primary);
}

#hero {
    display: none;
}

#landing-image {
    margin-bottom: 3rem;
}

#landing-image img {
    width: 100%;
}

#landing-panel main {
    width: 100%;
}

#welcome-text {
    margin-bottom: 2rem;
}

#buttons-container {
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 2rem;
}

#buttons-container > a {
    flex-basis: 40%;
}

#buttons-container .btn {
    display: flex;
    align-items: baseline;
    flex-direction: column;
    padding: 1rem;
}

#buttons-container .btn-grey {
    background-color: var(--color-primary-lighter);
}

#buttons-container .btn-grey:hover {
    background-color: var(--color-primary-light);
}

#buttons-container .btn img {
    margin-bottom: 3rem;
}

#landing-panel footer {
    width: 100%;
}

#landing-panel footer img {
    margin-left: .5rem;
}

@media(min-width: 1000px) {
    #landing-panel {
        min-width: 50rem;
        display: grid;
        grid-template-columns: 2fr 1fr 1fr;
        grid-template-areas:
        "hero header header"
        "buttons buttons footer";
    }
    #hero {
        display: block;
        grid-area: hero;
    }
    #hero img {
        width: 80%;
    }
    #landing-image {
        grid-area: header;
    }
    #landing-panel main {
        grid-area: buttons;
    }
    #welcome-text {
        display: none;
    }
    #buttons-container {
        justify-content: flex-start;
    }
    #buttons-container .btn {
        margin-right: 1rem;
    }
    #buttons-container .btn {
        flex-direction: row-reverse;
        padding-top: 2rem;
        padding-bottom: 2rem;
    }
    #buttons-container .btn img {
        display: none;
    }
    #landing-panel footer {
        display: flex;
        align-items: center;
        font-size: .6rem;
    }
}
#page-header {
    background-color: var(--color-primary);
    color: var(--color-title-in-primary);
    padding: 1rem;
    padding-bottom: 4rem;
    width: 100%;
}

#titles {
    width: 60%;
}

#title-max,
#title-min {
    margin-bottom: 2rem;
}

#page-header-icons {
    height: 1rem;
    /* max-width: 60rem; */
    width: 90%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
}

#page-header-icons img {
    height: 100%;
}

#title-max {
    display: none;
}

#desc {
    display: none;
}

@media(min-width: 900px) {
    #title-max {
        display: block;
    }
    
    #title-min {
        display: none;
    }

    #desc {
        display: block;
    }
}
.teacher-form {
    height: 100vh;
}

.teacher-form > .container,
#header-children,
#teacher-form-sm {
    height: 100%;
}

#teacher-form-sm {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

#teacher-form-sm-titles {
    margin: 0;
    flex: 1 1;
    width: 60%;
    margin-top: 3rem;
}

#teacher-form-sm > div h1 {
    width: 80%;
    margin-bottom: 2rem;
}

#teacher-form-sm .btn {
    text-align: center;
    width: 100%;
    margin-bottom: 1rem;
}

#teacher-form-lg {
    margin-top: -3rem;
    margin-bottom: 3rem;
    display: none;
}

#teacher-form-lg h2 {
    margin-bottom: 1rem;
}

#teacher-form-lg hr {
    margin-bottom: 2rem;
    color: var(--color-line-in-white);
}

.form-group textarea {
    resize: none;
    display: block;
    height: 10rem;
}

.teacher-form-time {
    display: grid;
    grid-column-gap: 1rem;
    grid-template-columns: 1fr 1fr 1fr;
}

#teacher-form-lg section {
    padding: 0 3rem;
}

#teacher-form-lg section:first-child {
    padding-top: 3rem;
}

#teacher-form-lg section:nth-child(3n) {
    padding-bottom: 3rem;
}

#teacher-form-footer {
    padding: 3rem;
    display: flex;
    justify-content: space-between;
    background-color: var(--color-box-footer);
    border-top: 2px solid var(--color-line-in-white);
}

#teacher-form-footer > div {
    display: flex;
    color: var(--color-text-base);
    align-items: center;
    font-size: .7rem;
    line-height: .8rem;
}

#teacher-form-schedules-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: .2rem;
}

#teacher-form-schedules-header .btn{
    background-color: var(--color-primary-lighter);
}

#teacher-form-schedules-header h2{
    margin-bottom: 0;
}

#teacher-form-schedules-header .btn:hover{
    background-color: var(--color-primary-light);
}

#teacher-form-footer img {
    margin-right: .5rem;
}

@media (min-width: 900px) {
    .teacher-form {
        height: auto;
    }
    #teacher-form-sm {
        display: none;
    }
    #teacher-form-lg {
        display: block;
    }
}
#favourites-footer {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
}

#favourites-footer {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

#favourites-footer button {
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--color-title-in-primary);
    background-color: var(--color-primary);
    border: none;
    cursor: pointer;
    padding: 2rem;
    transition: .5s;
}

#favourites-footer button:hover,
#favourites-footer button.active {
    background-color: var(--color-primary-dark);
}

#favourites-footer button img {
    height: 2.5rem;
    margin-right: 1rem;
}

@media (min-width: 900px) {
    
    #favourites-footer {
        display: none;
    }
}
#teacher-list-filter-button {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 1rem;
    border-bottom: 1px solid var(--color-text-in-primary);
    padding-bottom: .5rem;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
}

#teacher-list-filter-button img {
    height: 1rem;
}

#teacher-list-filter-collapse {
    overflow: hidden;
    height: 0;
    transition: height .5s;
}

#teacher-list-filter-collapse.open {
    height: 12rem;
}

#teacher-list-filter-menu-sm label{
    color: var(--color-text-in-primary)
}

#time {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: .5rem;
}

#time input {
    display: block;
    width: 100%;
}

@media (min-width: 900px) {
    #teacher-list-filter-button {
        display: none;
    }
}
.teacher-file:first-child {
    margin-top: -3rem;
}

.teacher-file:last-child {
    margin-bottom: 10rem;
}

.teacher-file {
    color: var(--color-text-base);
    margin-bottom: 3rem;
}

.teacher-file header {
    padding: 2rem 2rem 0 2rem;
    display: flex;
    align-items: center;
    margin-bottom: 1rem;
}

.teacher-file header .img-container {
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    overflow: hidden;
}

.teacher-file header .img-container img {
    width: 100%;
    object-fit: cover;
}

.teacher-file header .label {
    margin-left: 1rem;
}

.teacher-file header .label h2 {
    color: var(--color-text-title);
    font-size: 1rem;
}

.teacher-file header .label span {
    font-size: .8rem;
}

.teacher-file main p {
    padding: 0 2rem;
    font-size: .9rem;
    margin-bottom: 1rem;
}

.teacher-file footer {
    border-top: 1px solid var(--color-text-in-primary);
    background-color: var(--color-box-footer);
    padding: 2rem;
    display: flex;
    flex-direction: column;
    border-radius: 0 0 .5rem .5rem;
}

.teacher-file footer .price {
    display: flex;
    justify-content: center;
    margin-bottom: 1rem;
}

.teacher-file footer .price strong {
    margin-left: 1rem;
}

.teacher-file footer .fav {
    display: flex;
    align-items: center;
}

.fav-btn {
    background-color: var(--color-primary) !important;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 1rem;
    height: 4rem;
    width: 4rem;
}

.fav-btn.is-fav {
    background-color: var(--color-primary-darker) !important;
}

.fav-btn img {
    height: 1.5rem;
}

.whatsapp-btn {
    display: flex;
    height: 4rem;
    align-items: center;
    flex: 1 1;
}

.whatsapp-btn img {
    margin-right: 1rem;
}

@media (min-width: 900px) {

    .teacher-file:first-child {
        margin-top: 0
    }

    .teacher-file footer {
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }

    .fav-btn {
        display: none;
    }
}
#teacher-list-filter-menu-lg {
    margin-top: -3.5rem;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 1rem;
    display: none;
}

#teacher-list-filter-menu-lg label {
    color: var(--color-text-in-primary);
}

@media(min-width: 900px) {
    #teacher-list-filter-menu-lg {
        display: grid;
    }
}
:root {
    --color-background: #D9D9D9;
    --color-primary-lighter: #9871F5;
    --color-primary-light: #916BEA;
    --color-primary: #8257E5;
    --color-primary-dark: #774DD6;
    --color-primary-darker: #6842C2;
    --color-secondary: #04D361;
    --color-secondary-dark: #04BF58;
    --color-title-in-primary: #FFFFFF;
    --color-text-in-primary: #D4C2FF;
    --color-text-title: #32264D;
    --color-text-complement: #9C98A6;
    --color-text-base: #6A6180;
    --color-line-in-white: #D2D2DB;
    --color-input-background: #F8F8FC;
    --color-button-text: #FFFFFF;
    --color-box-base: #FFFFFF;
    --color-box-footer: #e3e3e9;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: "Poppins", sans-serif;
    outline: none;
}

html, body, #root, .App {
    min-height: 100vh;
    width: 100%;
}

body {
    background-color: #D9D9D9;
    background-color: var(--color-background);
}

a {
    text-decoration: none;
}

h1 {
    color: #FFFFFF;
    color: var(--color-title-in-primary);
}

.App {
    display: flex;
    flex-direction: column;
}

.container {
    width: 90%;
    max-width: 40rem;
    margin: 0 auto;
}

.btn {
    cursor: pointer;
    background-color: #04D361;
    background-color: var(--color-secondary);
    color: #FFFFFF;
    color: var(--color-button-text);
    padding: 1rem;
    border-radius: .2rem;
    border: none;
    transition: background .3s;
}

.btn:hover{
    background-color: #04BF58;
    background-color: var(--color-secondary-dark);
}

.panel {
    background-color: #FFFFFF;
    background-color: var(--color-box-base);
    border-radius: .5rem;
}

.form-group {
    margin-bottom: 1rem;
    display: flex;
    flex-direction: column;
    color: #6A6180;
    color: var(--color-text-base);
}

.form-group input,
.form-group textarea, 
.form-group select {
    display: block;
    padding: 1rem;
    border-radius: .5rem;
    border: 2px solid #D2D2DB;
    border: 2px solid var(--color-line-in-white);
    outline: none;
    background-color: #F8F8FC;
    background-color: var(--color-input-background);
}
